/* google fonts */
/* global */

::-webkit-scrollbar {
  width: 0;
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: "Raleway", sans-serif;
  margin: 0;
}

* {
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
}

.container {
  max-width: 1170px;
  margin: auto;
}
/* header section */
header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  padding: 10px 15px;
  z-index: 100;
}
header.sticky {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-animation: header 0.5s ease;
          animation: header 0.5s ease;
}
@-webkit-keyframes header {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@keyframes header {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
header .logo {
  float: left;
}
header .logo a {
  font-family: "Dancing Script", cursive;
  font-size: 30px;
  display: inline-block;
  padding: 5px 0px;
  color: #ffffff;
  font-weight: 600;
  letter-spacing: 2px;
}
header .navbar {
  float: right;
}
header .navbar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
header .navbar ul li {
  display: inline;
  padding: 0 15px;
}
header .navbar ul li a {
  display: inline-block;
  padding: 10px 0px;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
  position: relative;
}
header .navbar ul li a::before {
  content: "";
  position: absolute;
  top: 100%;
  margin-top: -5px;
  height: 2px;
  left: 0px;
  background-color: #ffffff;
  width: 0;
  transition: width 0.5s ease;
}
header .navbar ul li a.active::before {
  width: 100%;
}
header .navbar ul li a:hover::before {
  width: 100%;
}

/* home section */
.home {
  min-height: 100vh;
  background-image: url(/static/media/wallp.3c36fa5d.jpg);
  background-size: cover;
  background-position: center;
  float: left;
  width: 100%;
  padding: -15px 15px;
  position: relative;
}

.home::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  background-color: #000000;
  opacity: 0.3;
}

.home .home-text,
.home .home-scroll {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  z-index: 10;
}

.home .home-text h3 {
  font-size: 70vh;
  color: #ffffff;
  margin: 15px 0px 0px;
  font-weight: 700;
}
.home .home-scroll h2 {
  font-family: "Dancing Script", cursive;
  color: #ffffff;
  margin: 15px 0px 0px;
  font-weight: 400;
}

/*about Section*/

.about {
  float: left;
  width: 100%;
  padding: 80px 0px;
  background-color: #ffffff;
}

h2.profile-title {
  font-size: 30px;
  font-weight: 600;
  margin: 0;
  padding-bottom: 70px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  position: relative;
  letter-spacing: 0.5px;
}
h2.profile-title::before {
  content: "";
  position: absolute;
  width: 80px;
  height: 2px;
  left: 50%;
  margin-left: -40px;
  top: 50px;
  background-color: #000000;
}

.about .about-content {
  width: 100%;
  float: left;
}

.about .about-content .img {
  float: left;
  width: 35%;
  padding: 15px;
}
.about .about-content .img img {
  width: 100%;
  border: 10px solid #000000;
}
.about .about-content .description {
  float: left;
  width: 65%;
  padding: 15px;
}
.about .about-content .description h4 {
  
  font-size: 20px;
  color: #000000;
  margin: 0;
  padding-bottom: 10px;
  font-weight: 600;
}
.about .about-content .description h5 {
  font-size: 25px;
  color: #000000;
  margin: 0;
  padding-bottom: 10px;
  font-weight: 600;
}
.about .about-content .description .profile-info {
  color: black;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 0.5px;
  text-align: left;
  font-weight: 400;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.about .about-content .description p {
  color: black;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 0.5px;
  text-align: left;
  font-weight: 400;
}
.about .about-content .description p button {
  margin-right: 10px;
  margin-top: 20px;
}
.btn-light {
  padding: 12px 40px;
  border-radius: 0;
  font-size: 16px;
  color: #f3971b;
  background-color: transparent;
  font-weight: 400;
  border: 1px solid #f3971b;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: all 0.5s ease;
}
.btn-light:hover {
  color: #ffffff;
  background-color: #f3971b;
}
/*Skills section*/


