/* contact section */
.contact {
  float: left;
  width: 100%;
  padding: 80px 15px;
  background-color: white;
}

.contact .contact-content {
  margin: auto;
  max-width: 650px;
}

.contact .contact-content input,
.contact .contact-content textarea {
  height: 50px;
  border: none;
  border-left: 2px solid transparent;
  padding: 10px 15px;
  color: #ffffff;
  margin-bottom: 30px;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 400;
  display: block;
  width: 100%;
  background-color: rgb(0, 0, 0);
  transition: border 0.5s ease;
}
.contact .contact-content input:focus,
.contact .contact-content textarea:focus {
  border-color: rgb(0, 0, 0);
}
.contact .contact-content textarea {
  min-width: 100%;
  max-width: 100%;
  min-height: 150px;
}
.contact .contact-content button {
  display: block;
  margin: auto;
}
h2.contact-title {
  font-size: 30px;
  font-weight: 600;
  margin: 0;
  padding-bottom: 70px;
  text-align: center;
  text-transform: uppercase;
  color: black;
  position: relative;
  letter-spacing: 0.5px;
}
h2.contact-title::before {
  content: "";
  position: absolute;
  width: 80px;
  height: 2px;
  left: 50%;
  margin-left: -40px;
  top: 50px;
  background-color: black;
}
.contact-btn-bg {
  padding: 12px 40px;
  border-radius: 0;
  font-size: 16px;
  color: white;
  font-weight: 400;

  background-image: url(/static/media/wallp.3c36fa5d.jpg);
  background-size: cover;
  border: 1px solid transparent;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: all 0.5s ease;
}
.contact-btn-bg:hover {
  border: 1px solid rgb(0, 0, 0);
  color: rgb(200, 200, 200);
  background-color: transparent;
}

/*Footer Section*/

footer {
  float: left;
  width: 100%;
  background-color: #000000;
  padding: 50px 0px;
}

footer .footer-content {
  float: left;
  width: 100%;
}
footer .social-links {
  float: left;
  width: 100%;
  text-align: center;
  border-top: 1px solid #ffffff;
  padding-top: 15px;
}
footer .social-links ul {
  padding: 0;
  list-style: none;
  margin: 0;
}
footer .social-links ul li {
  display: inline;
}
footer .social-links ul li a {
  display: inline-block;
  padding: 15px 15px;
}
footer .social-links ul li a span {
  display: inline-block;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid white;
  color: white;
  line-height: 38px;
  text-align: center;
  font-size: 16px;
  transition: all 0.5s ease;
}
footer .social-links ul li a span:hover {
  background-color: white;
  color: black;
}
footer .footer-content .box {
  width: 33.33%;
  float: left;
  padding: 15px;
  text-align: center;
}
footer .footer-content .box .icon span {
  color: white;
  font-size: 30px;
}
footer .footer-content .box h4 {
  font-size: 16px;
  color: rgb(180, 180, 180);
  margin: 0;
  font-weight: 600;
  text-transform: uppercase;
  padding: 20px 0 0px;
}
footer .footer-content .box p {
  color: rgb(100, 100, 100);
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 0.5px;

  font-weight: 400;
  padding: 10px 0;
  margin: 0px;
}

/* RESPONSIVE */

.trigger-main {
  position: absolute;
  top: 15px;
  right: 0px;
  display: none;
}
.trigger-main a {
  display: inline-block;
  position: relative;
  z-index: 210;
}
.trigger-main a span {
  display: block;
  height: 2px;
  width: 30px;
  background-color: #ffffff;
  margin-bottom: 8px;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transition: transform 0.5s ease;
}
.trigger-main a span:nth-child(1) {
  -webkit-transform-origin: left top;
          transform-origin: left top;
}
.trigger-main a span:nth-child(2) {
  -webkit-transform-origin: left bottom;
          transform-origin: left bottom;
}
.trigger-main a.active span:nth-child(1) {
  -webkit-transform: rotate(45deg) translateY(-7px);
  transform: rotate(45deg) translateY(-7px);
}
.trigger-main a.active span:nth-child(2) {
  -webkit-transform: rotate(-45deg) translateY(7px);
  transform: rotate(-45deg) translateY(7px);
}
/*Responsive*/

@media (max-width: 991px) {
  .trigger-main {
    display: block;
  }
  header .container {
    position: relative;
  }
  .container {
    max-width: 750px;
  }
  .services .services-content .card .box h3 {
    min-height: 50px;
  }

  header {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.8);
  }

  header .navbar {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 180px;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 200;
    padding-top: 80px;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    transition: -webkit-transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
    -webkit-transition: -webkit-transform 0.5s ease;
  }
  header .navbar.open {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  header .navbar ul li {
    display: block;
  }
}
@media (max-width: 767px) {
  .home {
    display: -webkit-flex;
    display: flex;
    max-width: 100%;
  }
  .home .home-text h3 {
    margin-top: 20vh;
    font-size: 20vh;
  }
  .home .home-text {
    left: 0;
    padding: 0px 15px;
  }
  .home .home-scroll {
    margin-top: 25vh;
  }
  .about .about-content .img{
    width: 100%;
  }
  .about .about-content .description{
    width: 100%;
  }
  .skills .skills-content .card{
    width: 100%;
  }
}
@media (max-width: 500px) {
  .home .home-text {
    top: 55%;
  }
  .home {
    width: 100%;
  }
  footer .footer-content .box {
    width: 100%;
  }
}

